import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { useDispatch } from 'react-redux';
import { Button, withStyles } from '@material-ui/core';

import { authLogout } from '../../store/actions/actionProfile';

export interface Props {}

const useStyles = makeStyles((theme) => ({
  logoutContainer: {
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
  },
  heading: {
    fontSize: '18px',
    color: '#669900',
  },
  logoutButton: {
    height: 55,
    marginTop: 15,
    width: 250,
    borderRadius: '6px',
    color: '#fff',
    fontSize: '16px',
    textTransform: 'capitalize',
    backgroundColor: '#000',
    ':hover': {
      color: '#000',
    },
  },
}));

const StyledButton = withStyles((theme) => ({
  root: {
    height: 55,
    marginTop: 15,
    width: 250,
    borderRadius: '6px',
    color: '#fff',
    fontSize: '16px',
    textTransform: 'capitalize',
    backgroundColor: '#000',
    boxShadow: '0 0 0 1px rgb(0 0 0 / 5%), 0 5px 15px rgb(0 0 0 / 15%)',
    '&:hover': {
      color: '#000',
    },
  },
  endIcon: {
    margin: 0,
  },
}))((props: any) => <Button disableRipple {...props} />);

export const Logout: React.FC<Props> = () => {
  const classes = useStyles();
  const dispatch = useDispatch();

  const handleLogout = () => {
    dispatch(authLogout());
  };

  return (
    <div className={classes.logoutContainer}>
      <h3 className={classes.heading}>You're already signed in.</h3>
      <StyledButton onClick={handleLogout}>Logout</StyledButton>
    </div>
  );
};

export default Logout;
